  font-size: 20px;
  font-weight: 300;
}

#aboutMe-sec .backGround {
  z-index: -1;
  background-color: #6200ff;
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  transform: translateY(-100%);
}

#aboutMe-sec .backGround .shape {
  filter: blur(5px);
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
}

#aboutMe-sec .backGround .circle {
  aspect-ratio: 1;
  background-color: #fff;
  border-radius: 50%;
  width: 100px;
  animation-name: backGround-circle;
  animation-duration: 15s;
}

#aboutMe-sec .backGround .square {
  aspect-ratio: 1;
  background-color: #fff;
  width: 100px;
  animation-name: backGround-square;
  animation-duration: 9s;
}

#aboutMe-sec .backGround .triangle {
  border-bottom: 100px solid #fff;
  border-left: 50px solid #0000;
  border-right: 50px solid #0000;
  width: 0;
  height: 0;
  animation-name: backGround-triangle;
  animation-duration: 20s;
  display: block;
}

#aboutMe-sec .backGround .rectangular {
  aspect-ratio: 2;
  background-color: #10d0b6;
  width: 200px;
  animation-name: backGround-rectangular;
  animation-duration: 20s;
}

@keyframes backGround-circle {
  0% {
    transform: translateX(-100%)translateY(50%);
  }

  100% {
    transform: translateX(calc(100vw + 100%))translateY(-10%);
  }
}

@keyframes backGround-square {
  0% {
    transform: translateX(-100%)translateY(120%);
  }

  100% {
    transform: translateX(calc(100vw + 30%))translateY(-150%);
  }
}

@keyframes backGround-rectangular {
  0% {
    transform: translateX(-100%)translateY(0%);
  }

  100% {
    transform: translateX(calc(100vw + 100%))translateY(350%);
  }
}

@keyframes backGround-triangle {
  0% {
    transform: translateX(calc(100vw + 100%))translateY(100%);
  }

  100% {
    transform: translateX(-100%)translateY(0);
  }
}

